.pricingContainer {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  margin: 20px;
}

.pricingCard {
  flex: 1;
  border: 1px solid #445d9c;
  border-radius: 5px;
  padding: 20px;
  text-align: center;
  transition: 0.3s;
  width: 200px;
  margin: 10px;
  background-color: #1a2e35;
  position: relative;
  display: flex;
  flex-direction: column; /* Cambia el flujo de elementos a columna */
}

.pricingCard h2 {
  color: #ffff;
  font-size: 24px;
}

.pricingCard p {
  font-size: 40px;
  color: #92d673;
  margin: 10px 0;
  font-weight: bold;
}

.pricingCard ul {
  padding: 0;
  margin: 10px 0;
  text-align: left;
}

ul {
  list-style: none;
}

ul li:before {
  content: '>> ';
}

.pricingCard li {
  margin: 5px 0;
  color: #ffff;
  border-bottom: 1px solid #425961;
  padding-bottom: 10px;
}

.cardFooter {
  margin-top: auto; /* Empuja el pie de la tarjeta hacia abajo */
}

.availablePlaces {
  font-weight: bold;
  color: wheat;
  letter-spacing: 1px;
  font-size: 12px;
}

.btnSelect {
  background-color: #ea4aaa;
  border-radius: 15px;
  color: #fff;
  font-size: 1em;
  font-weight: bold;
  margin: 10px;
  overflow: hidden;
  padding: 0.875em;
  cursor: pointer;
  min-width: 100px;
  border: none;
}

.btnSelect:hover {
  background-color: #5f2045;
  min-width: 120px;
}


