.titleContainer {
    display: flex;
    justify-content: center;
    color: white;
    margin-top: 30px;
    align-items: center;
}

.spread {
    margin-top: 50px;
}

.titleImage {
    width: 50px;
    height: 50px;
    padding: 10px;
}

.titleTsc {
    color: white;
}

  .socialMediaContainer{
    background-color: rgba(160, 160, 160, 0.5);
    border-radius: 5px;
    
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin: auto;
    padding: 10px;

    max-width: fit-content;
  }

  .socialMediaContainer p{
    color: #fff;
    font-size: 1rem;
    letter-spacing: 1px;
  }

  .sectionTitle {
    size: 15px;
    color: #00cfc8
  }

  .sectionContainer {
    margin: auto;
    width: 40%;
  }

@media screen and (max-width: 600px) {
    .titleContainer {
        justify-content: start;
        margin-top: 0px;
    }
    .titleTsc {
        visibility: hidden;
    }

    .sectionContainer {
        margin: auto;
        width: 80%;
      }
    
  }