.container {
    margin-top: 50px;
    background-color: #1a2e35;
    border: solid 1px;
    border-radius: 5px;
    border-color: #445d9c;

    margin: auto;
    width: 80%;
    padding: 10px;

    min-height: 100px;

    max-width: 800px;
}

.text {
    border-right: .15em solid orange;
    /* font-family: monospace; */
    color: #ffff;
    font-size: 1.1rem;
    /* letter-spacing: .15em; */
    animation: typing 1s steps(40) infinite, blink-caret .75s step-end infinite;
    white-space: pre-line;
}

@keyframes typing {
    from {
        width: 0
    }

    to {
        width: 100%
    }
}

@keyframes blink-caret {

    from,
    to {
        border-color: transparent
    }

    50% {
        border-color: orange
    }
}